//== Colors
//
//## Main colors for use across project.

$white:                 #FFFFFF;
$black:                 #000000;
$gray:                  #f0f0f0;             
$gray-light:            #626262;
$gray-more-light:       #b4ad9e; 

$blue-base:             #626262; 
$blue-dark:             #626262;
$blue-darker:           #626262;
$brown-base:            #626262;
$brown-light:           #626262;
$brown-lighter:         #626262;   
$gray-dark:             #626262;
$gray-darken:           #626262;  
$gray-darker:           #626262;      
        


//== Scaffolding
//
//## Settings for some of the most global styles.

//** Background color for `<body>`.
$body-bg:               $white;
//** Global text color on `<body>`.
$text-color:            $gray-light;
$label-color:           $white;

//** Global textual link color.
$link-color:            $gray-light;;
$link-hover-color:      #CACACA;
$link-active-color:     #CACACA;


//== Typography
//
//## Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:  'PlayfairDisplay', Arial, sans-serif;
$font-ubuntu:             'Ubuntu', Arial, sans-serif;
$font-family-base:        $font-family-sans-serif;
$font-family-second:      $font-ubuntu;

$font-size-browser:       16; 
$font-size-base:          14;

$line-height-base:        21 / $font-size-base; 


